/* App.css */
.app {
    text-align: center;
  }
  
  .maze {
    display: inline-block;
    border: 2px solid #000;
  }
  
  .row {
    display: flex;
  }
  
  .cell {
    width: 20px;
    height: 20px;
    background-color: #fff;
    border: 1px solid #ccc;
  }
  
  .wall {
    background-color: #000;
  }
  
  .player {
    background-color: #f00;
  }
  
  .end {
    background-color: #00f;
  }
  
  .path {
    background-color: #0f0;
  }
  
  .controls {
    margin-top: 10px;
  }
  
  button {
    margin: 5px;
  }